import {ModuleWithProviders, NgModule} from '@angular/core';
import {AdaptIconComponent} from './icon.component';

@NgModule({
  imports: [],
  declarations: [AdaptIconComponent],
  exports: [AdaptIconComponent]
})

export class AdaptIconModule {
  /**
   * Importing with '.forRoot()' is no longer necessary, you can simply import the module.
   * Will be removed in a short while
   *
   * @deprecated 11.16.0
   */
  static forRoot(): ModuleWithProviders<AdaptIconModule> {
    return {
      ngModule: AdaptIconModule
    };
  }
}

export * from './icon.component';
export * from './icon.config';
